import { useFormik } from 'formik';
import React from 'react';

import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import Divider from '@material-ui/core/Divider';
import FormControl from '@material-ui/core/FormControl';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormLabel from '@material-ui/core/FormLabel';
import Grid from '@material-ui/core/Grid';
import RadioGroup from '@material-ui/core/RadioGroup';
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';

import Radio from '../../components/Radio';
import { ColumnColor } from '../../constants';
import type { Column } from '../../types';

type ColumnFormProps = {
  column?: Partial<Column>;
  onSubmit: any;
  onCancel: any;
  disabled?: boolean;
  formTitle?: string;
  enableWipLimit?: boolean;
};

export function ColumnForm(props: ColumnFormProps) {
  const {
    column,
    disabled,
    formTitle = ' ()',
    onSubmit,
    onCancel,
    enableWipLimit = false,
  } = props;

  const { values, errors, handleChange, handleSubmit } = useFormik({
    initialValues: Object.assign(
      {
        listGroupTitle: '',
        desc: '',
        color: '',
        wipEnabled: false,
        wipLimit: 0,
      },
      column,
    ),
    onSubmit: (values) => {
      onSubmit && onSubmit(values);
    },
    validate: (values) => {
      const errors: any = {};
      if (!values.listGroupTitle) {
        // errors.title = t('titleRequired');
        errors.listGroupTitle = '* ';
      }

      if (values.wipEnabled && !values.wipLimit) {
        // errors.wipLimit = t('limitRequired');
        errors.wipLimit = '*  ';
      }

      return errors;
    },
  });

  const handleWipLimitChange = React.useCallback(
    (e) => {
      e.persist();
      const { value } = e.target;
      const integerRegex = /^[0-9]*$/;
      if (value && !integerRegex.test(value)) {
        return;
      }

      handleChange(e);
    },
    [handleChange],
  );

  return (
    <form onSubmit={handleSubmit}>
      <Grid container spacing={1}>
        <Grid item xs={12}>
          <Typography gutterBottom variant='h6'>
            {formTitle}
          </Typography>
          <Divider />
        </Grid>
        <Grid item xs={12}>
          <TextField
            name='listGroupTitle'
            label={''}
            value={values.listGroupTitle}
            error={Boolean(errors.listGroupTitle)}
            helperText={errors.listGroupTitle}
            disabled={disabled}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          <TextField
            multiline
            rows={3}
            name='desc'
            label={''}
            value={values.desc}
            error={Boolean(errors.desc)}
            helperText={errors.desc}
            disabled={disabled}
            onChange={handleChange}
          />
        </Grid>
        <Grid item xs={12}>
          {enableWipLimit ? (
            <FormControlLabel
              control={
                <Checkbox
                  checked={values.wipEnabled}
                  onChange={handleChange}
                  name='wipEnabled'
                />
              }
              label={'wipLimitEnabled'}
            />
          ) : null}
        </Grid>
        <Grid item xs={12}>
          {enableWipLimit ? (
            <TextField
              name='wipLimit'
              label={'wipLimit'}
              value={values.wipLimit}
              error={Boolean(errors.wipLimit)}
              helperText={errors.wipLimit}
              disabled={disabled || !values.wipEnabled}
              onChange={handleWipLimitChange}
            />
          ) : null}
        </Grid>

        <Grid item xs={12}>
          <FormControl component='fieldset'>
            <FormLabel component='legend'></FormLabel>
            <RadioGroup
              row
              aria-label='background'
              name='color'
              value={values.color}
              onChange={handleChange}
            >
              {Object.keys(ColumnColor).map((key) => {
                const colorKey = key as keyof typeof ColumnColor;
                return (
                  <Radio
                    key={colorKey}
                    value={colorKey}
                    color={ColumnColor[colorKey]}
                  />
                );
              })}
            </RadioGroup>
          </FormControl>
        </Grid>
        <Grid item xs={12}>
          <Button variant='outlined' disabled={disabled} onClick={onCancel}>
            
          </Button>
          &emsp;
          <Button
            type='submit'
            color='primary'
            variant='contained'
            disabled={disabled}
          >
            
          </Button>
        </Grid>
      </Grid>
    </form>
  );
}

export default ColumnForm;
